

.todo{
  padding: 20px;
  word-break: break-word;

  .todo-header{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 30px;

    .todo-title{
      margin-left: 10px;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      display: flex;
      align-items: center;
      color: #FFFFFF;
    }
    .todo-title__input{
      outline: none;
      margin: unset;
      height: 10px;
      width: 80%;
      display: flex;
      word-break: break-word;
    }

    .menu-icon{
      cursor: pointer;
      height: 20px;
      width: 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;


      &:hover .todo-dot{
        background-color: #fff;
      }

      .todo-dot{
        height: 3px;
        width: 3px;
        border-radius: 50%;
        background-color: grey;
        transition: all ease-in-out .3s;
      }
    }
  }


  .todo-task-group{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;

    .todo-label{
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
    }

  }

  .todo-main{
    padding-right: 10px;
    overflow-y: auto;
    overflow-x: hidden;
    height: 250px;
  }


}

.todo-shadow {
  width: 280px;
  min-height: 250px;
  display: flex;
  padding: 10px 15px;
  background-color: grey;
  border-radius: 5px;

  .shadow-content {
    visibility: hidden;
  }
}
